.TransactionForm{
  margin-top: 40px;
  height:50vh;
  background:var(--form-background);
  float:right;
  width:44%;
  margin-right:2%;
  border-radius: 20px;
  padding:10px;
  margin-bottom:10px;
  display:table;
}
.TransactionForm__Body{
  display: table-cell;
  text-align: center;
  vertical-align: middle;
  
}
.TransactionForm__Title{
  margin-top:0px;
  text-align:center;
}
.TransactionForm__Form{
  margin-top:40px;
  text-align:center;
  
}
.TransactionForm__Inputs{
  display:block;
  margin-left:50%;
  transform:translateX(-50%);
  background: var(--form-background);
  background-image: linear-gradient(
    0deg,
    rgba(0,0,0,0.5) 0%,
    rgba(0,0,0,0.5) 100%);
}
.TransactionForm__Name,
.TransactionForm__Cost{
  border:solid 1.2px var(--border-color);
  height:30px;
  padding-left: 10px;
}
.TransactionForm__Button{
  font-family: var(--fontfamily-buttons);
  color:var(--text-color);
  background: var(--button-color);
  border:none;
  box-shadow: 3px 3px 2px 2px black;
  transition-duration:1s;
  border-radius:8px;
  padding:10px;
  cursor:pointer;
  
  font-size:17px;
}
.TransactionForm__Button:hover{
  transform:scale(1.2)
  translateX(-42%);
}

.TransactionForm__Spans{
  position:relative;
  top:-10px;
}
.TransactionForm__CostValidation,
.TransactionForm__NameValidation{
  margin-top:5px;
  margin-bottom:40px;
}


