.Valute{
  margin-top:4vh;
  height:23vh;
  background:var(--valute-background);
  float:right;
  width:44%;
  margin-right: 2% ;
  border-radius: 20px;
  padding:10px;
  text-align:center;
  display:table;
}
.Valute__Box{
  display: table-cell;
  text-align: center;
  vertical-align: middle;
}
.Valute__BankValute{
  text-align:center;
  width:100%;
  margin-top:0px;
}
.Valute__ActualyValute{
  text-align:center;
}
.Valute_ActualyValuteValue{
  font-size:12px;
}

.Valute__ActualyValute input{
  margin: 0px 5px 0px 10px;
  border:solid 1.2px var(--border-color);
  color:white;
  padding:3px;
  background:none;
  width:100px;
  background-image: linear-gradient(
    0deg,
    rgba(0, 0, 0, 0.5) 0%,
    rgba(0, 0, 0, 0.5) 100%);
  
}
.Valute__ButtonChange{
  font-family: var(--fontfamily-buttons);
  color:var(--text-color);
  background: var(--button-color);
  border:none;
  box-shadow: 3px 3px 2px 2px black;
  transition-duration:1s;
  border-radius:8px;
  margin-top:20px;
  cursor:pointer;
  padding:8px;
  font-size:15px;
}
.Valute__ButtonChange:hover{
  transform:scale(1.2);
}

