.TransactionList{
  border-radius: 20px;
  height:90vh;
  background:var(--resoult-background);
  float:left;
  width: 48%;
  padding-left:1%;
  margin-left:1%;
  /* border:solid 15px purple; */
  padding:10px;
  text-align:center;
  
}
.TransactionList_ValueBox{
  text-align:center;
  height: calc( 66vh - 4em );
  margin-bottom:2%;
  overflow:scroll;
  scrollbar-color: rgb(255, 255, 255) var(--resoult-background);
  scrollbar-width: thin;
  overflow-x:hidden;
}
.highestTrans{
  margin-top:5px;
  min-height: 52.8px;
  margin-bottom:15px;
}
.heighestTrans span{
    top: 21px;
    position: relative;
}


.highestTrans_EUR,
.highestTrans_PLN{
  text-align:center;
  padding-right: 5px;
}
.TableOneItem__Title{
  background:var(--listTitle-backgorund);
  position:sticky;
  top:0;
  opacity:1;
}
.TableOneItem{
  text-align:center;
  width:99%;
  background:var(--list-backgorund);
  border-spacing: 0px;
}
.TableOneItem__CostEUR,
.TableOneItem__CostPLN{
  text-align:right;
  padding-right:5px;
}
.TableOneItem__Delete{
  background:none;
  border:none;
  cursor: pointer;
  color:white;
  transition-duration:1s;
}
.TableOneItem__Delete:hover{
  color:var(--validation-color);
}
.SumTrans{
  width:49%;
 display:inline;
 float:left;
}

.Button__DeleteAll{
  font-family: var(--fontfamily-buttons);
  color:var(--text-color);
  background: var(--button-color);
  border:none;
  box-shadow: 3px 3px 2px 2px black;
  transition-duration:1s;
  border-radius:8px;
  float:right;
  margin-right:15px;
  cursor:pointer;
  padding:8px;
  font-size:15px;
}
.Button__DeleteAll:hover{
  transform:scale(1.2);
}

.Sum_Table{
  background: var(--list-backgorund);
  width:100%;
  margin-bottom:10px;
  border-spacing: 0px;
}
.Sum__Title{
  width:50%;
}
.Sum__Box{
position:absolute;
width:48%;
bottom:7vh;
}